import getService from "../../services";

export function loadMatches(id) {
  debugger;
  return dispatch => {
    return getService(`http://localhost:5000/api/matches/${id}`, "GET")
      .then(response => {
        dispatch(matches(response.data));
      })
      .catch(e => console.log(e));
  };
}

export function matches(matches) {
  return { type: "LOAD_MATCHES", matches: matches };
}

export function loadDeliveries(id) {
  return dispatch => {
    return getService(`http://localhost:5000/api/deliveries/${id}`, "GET")
      .then(response => {
        dispatch(deliveries(response.data));
      })
      .catch(e => console.log(e));
  };
}

export function deliveries(deliveries) {
  return { type: "LOAD_DELIVERIES", deliveries: deliveries };
}

export function pageInc() {
  return dispatch => {
    return dispatch({ type: "PAGE_INC" });
  };
}
export function pageDec() {
  return dispatch => {
    return dispatch({ type: "PAGE_DEC" });
  };
}
